<template>
  <div style="width: 100%;height:100%;" :id="uuid"></div>
</template>

<script>
import cityPosition from '@/assets/map/cityPosition.json'
export default {
  name: "cpt-chart-mapMigrate",
  title: "迁徙地图",
  icon: 'el-icon-map-location',
  initWidth: 350,
  initHeight: 350,
  group: 'map',
  props:{
    width:Number,
    option:Object
  },
  data(){
    return {
      uuid:'',
      chartOption:{},
      chart:undefined,
    }
  },
  watch:{
    width(){
      this.chart.resize();
    }
  },
  created() {
    this.uuid = require('uuid').v1();
  },
  mounted() {
    this.chart = this.$echarts.init(document.getElementById(this.uuid));
    this.loadChart(this.option);
  },
  methods: {
    loadChart(option) {
      const startPosition = {name:'北京',position:[116.4551,40.2539]}
      const targetData = [
        {name: '上海', value: 95},
        {name: '广州', value: 90},
        {name: '大连', value: 80},
        {name: '南宁', value: 70},
        {name: '南昌', value: 60},
        {name: '拉萨', value: 50},
        {name: '长春', value: 40},
        {name: '包头', value: 30},
        {name: '重庆', value: 20},
        {name: '乌鲁木齐', value: 100}
      ];


      const planePath = 'path://M1705.06,1318.313v-89.254l-319.9-221.799l0.073-208.063c0.521-84.662-26.629-121.796-63.961-121.491c-37.332-0.305-64.482,36.829-63.961,121.491l0.073,208.063l-319.9,221.799v89.254l330.343-157.288l12.238,241.308l-134.449,92.931l0.531,42.034l175.125-42.917l175.125,42.917l0.531-42.034l-134.449-92.931l12.238-241.308L1705.06,1318.313z';

      const convertData = function (toData) {
        const res = [];
        toData.forEach(item => {
          const toPosition = cityPosition[item.name];
          if (toPosition) {
            res.push([startPosition.position,toPosition]);
          }
        })
        return res;
      };

      const seaData = function (toData){
        const res = [];
        toData.forEach(item => {
          let value = cityPosition[item.name].concat(item.value)
          res.push({name:item.name,value:value})
        })
        return res;
      }

      const that = this;
      that.chartOption = {
        backgroundColor: option.backgroundColor,
        title : {
          text: option.titleText,
          subtext: option.subtext,
          left: 'center',
          textStyle : {
            color: '#fff'
          }
        },
        tooltip : {
          trigger: 'item'
        },
        geo: {
          map: 'china',
          label: {
            show: true,
            color: "#aaa"
          },
          zoom: 1.23,
          roam: option.roam,//允许缩放
          itemStyle: {
            areaColor: '#323c48',
            borderColor: '#404a59',
          }
        },
        series: [{
          name: option.seriesName,
          type: 'lines',
          zlevel: 1,
          effect: {
            show: true,
            period: 6,
            trailLength: 0.7,
            color: '#fff',
            symbolSize: 3
          },
          lineStyle: {
            color: option.seriesColor,
            width: 0,
            curveness: 0.2
          },
          data: convertData(targetData)
        },
          {
            name: option.seriesName,
            type: 'lines',
            zlevel: 2,
            effect: {
              show: true,
              period: 6,
              trailLength: 0,
              symbol: planePath,
              symbolSize: 15
            },
            lineStyle: {
              color: option.seriesColor,
              width: 1,
              opacity: 0.4,
              curveness: 0.2
            },
            data: convertData(targetData)
          },
          {
            name: option.seriesName,
            type: 'effectScatter',
            coordinateSystem: 'geo',
            zlevel: 2,
            rippleEffect: {
              brushType: 'stroke'
            },
            label: {
              show: true,
              position: 'right',
              formatter: '{b}'
            },
            symbolSize: function (val) {
              return val[2] / 8;
            },
            itemStyle: {
              color: option.seriesColor
            },
            data: seaData(targetData)
          }]
      }
      that.chart.setOption(that.chartOption);
    }
  }
}
</script>

